@import url(https://fonts.googleapis.com/css?family=Lobster);

body{
  width:100%;
  height:100%;
}
#top-container{  
    width:100%;
    padding-bottom:14%;
    background-color: #8eb490;    
}
#logo{ 
    font-family: 'Lobster', cursive;
    position: absolute;
    left: 10%;
    font-size: 200%;
}

#logo h1 {
  color: #D5E2D6;
  text-align: center;
  line-height: 35px;
  margin-top: -180px;
  margin-right: 10px;
  -moz-transform: scale(1) rotate(-5deg) translateX(0px) translateY(0px) skewX(1deg) skewY(1deg);
-webkit-transform: scale(1) rotate(-5deg) translateX(0px) translateY(0px) skewX(1deg) skewY(1deg);
-o-transform: scale(1) rotate(-5deg) translateX(0px) translateY(0px) skewX(1deg) skewY(1deg);
-ms-transform: scale(1) rotate(-5deg) translateX(0px) translateY(0px) skewX(1deg) skewY(1deg);
transform: scale(1) rotate(-5deg) translateX(0px) translateY(0px) skewX(1deg) skewY(1deg);
  /*-----------------------*/
  text-shadow: 1px 1px #4A744D, 2px 2px #4A744D, 3px 3px #4A744D, 4px 4px #4A744D, 5px 5px #4A744D, 6px 6px #4A744D, 7px 7px #4A744D, 8px 8px #4A744D, 9px 9px #4A744D, 10px 10px #4A744D, 11px 11px #4A744D, 12px 12px #4A744D, 13px 13px #4A744D, 14px 14px #4A744D, 15px 15px #4A744D, 16px 16px #4A744D, 17px 17px #4A744D;
  
}

#logo span:nth-child(1), span:nth-child(7) { 
  font-size:150%;
}
#logo span:nth-child(5) { 
  margin-left: 15%; 
}

#logo-circle {
    height: 200px;
    width: 200px;
    border-radius: 100%;
    margin: auto;
    background: #4E8953;
}
.content{
  padding-top: 20px;
}
.form{
  position: absolute;
  left:30%;
  width:40%;
  top:20%;
  background-color: #dfdfdf;
}
.hr-trick {
  display: flex;
  /* flex-direction: row; */
  margin: 15px auto;
}
.hr-trick:before, .hr-trick:after{
  content: "";
  flex: 1 1;
  border-bottom: 1.5px solid;
  margin: auto;
}
.hr-trick:before {
  margin-right: 10px
}
.hr-trick:after {
  margin-left: 10px
}
.tab{
  margin-left:2em;
}
.tab-right{
  margin-right:2em;
}
.error-message{
  color:red;
}
.topic{
  padding: 0.5em;
  text-align:center;
  text-decoration: underline;
  opacity: 0.8;
}
.thumbnail{
    border: 1px solid #ddd; /* Gray border */
    border-radius: 4px;  /* Rounded border */
    padding: 5px; /* Some padding */
    width: 150px; /* Set a small width */
}
/* Add a hover effect (blue shadow) */
.thumbnail:hover {
  box-shadow: 0 0 2px 1px rgba(0, 140, 186, 0.5);
}

#hello{
  position: absolute;
  top:0;
  right:0;
  margin-right: 11rem;
  margin-top: 1rem;
  font-weight: 600;
  padding: 8px;
  border: 2px solid black;
  background-color: #D5E2D6;
}

#container-chat{
  position: absolute;
  left:35%;
  width:40%;
  top:25%;
  background-color: #ffe1b5;
}

#container-recipients{
  position: absolute;
  left:20%;
  width:20%;
  top:25%;
  /* background-color: #ffe1b5; */
}

.icon{
  position: absolute;
  font-size: 1.5rem;
  top:0;
  right:10px;
  cursor: pointer;
}
.logout{
  position: absolute;
  top:0;
  right:0;
  margin-right: 4rem;
  margin-top: 1rem;
  font-weight: 600;
  padding: 8px;
  border: 2px solid black; 
}

.chat-item{
  cursor: pointer;
}

#chat-body{
  background-color: lightyellow;
  width: 100%;
  height: 35rem;
}